.outer
{
    display:flex;
    justify-content: space-between;
    height:50px;
    border-bottom: 1px solid gainsboro;
    box-shadow: 2px 3px 3px gainsboro;
    background-color: ghostwhite;
}

.logo
{
    margin:16px;
    cursor: pointer;
    display:flex;
    flex-direction:row;
}

.login_options
{
    display:flex;
    justify-content: space-evenly;
}

.login_options>div
{
    padding-left:5px;
    padding-right: 5px;
    padding-top: 15px;
    height: 38px;
}


.a
{
    text-decoration: none;
    color:black;
    padding: 10px;
    transition: 0.1s ease-in-out;
}

.a:hover
{
    color:white;
    background-color: black;
    opacity: 0.3;
    border-radius: 8px;
}

