.box
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border:1px solid gainsboro;
    width: 250px;
    position: absolute;
    top:25%;
    right: 40%;
    padding:25px;
    box-shadow: 2px 3px 3px gainsboro;
    border-radius: 10px;
}

label
{
    margin-bottom: 3px;
    font-size: 13px;
}

input
{
    border:none;
    border-bottom: 1px solid black;
   padding:5px;
}

.first_name, .last_name, .email, .password{
    display: flex;
    flex-direction: column;
    margin: 5px;
    width:250px
}

button
{
    padding:15px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    background-color: white;
    color:black;
    border:none;
    box-shadow: 3px 4px 4px gainsboro;
    font-size: 15px;
    border-radius: 9px;
}

input:focus
{
    outline: none;
    border-color: rosybrown;
}

button:hover
{
    color:white;
    background-color: black;
    box-shadow: 3px 4px 4px black;
}