.checker_outer {
    display: flex;
    flex-direction: row;
    border:1px solid gainsboro;
    width: 95vw;
    justify-content:space-between;
    position: absolute;
    height: 85vh;
    top:10%;
    right: 2%;
    padding:10px;
    box-shadow: 2px 3px 3px gainsboro;
    border-radius: 10px;
    background: rgb(93,37,9);
    background: linear-gradient(40deg, rgba(93,37,9,0.630765685180322) 0%, rgba(142,126,36,0.45989733784138653) 100%);
}

h1
{
    color:white;

}

hr
{
    color:white;
}

h5{
    color:white;
    font-size:15px;  
}

.tabular{
    width: 80%;
    border-right:1px solid white;
}

#date_pckr
{
    width:20%;
    margin-right:1%;
}

label
{
    color:white;
    margin-right:1%;
