.box1
{
    display: flex;
    flex-direction: row;
    border:1px solid gainsboro;
    width: 95vw;
    justify-content:space-between;
    position: absolute;
    height: 85vh;
    top:10%;
    right: 2%;
    padding:10px;
    box-shadow: 2px 3px 3px gainsboro;
    border-radius: 10px;
    background: rgb(93,37,9);
    background: linear-gradient(40deg, rgba(93,37,9,0.630765685180322) 0%, rgba(142,126,36,0.45989733784138653) 100%);

}

hr
{
    color:white;
}



.classes
{
    width:80%;
    color:white;
    border-right: 1px solid white;

}

.new_classes
{
    color:white;
    padding-right: 7%;
    padding-top:0.5%;
    display:flex;
    flex-direction:column;
}

input
{
    width:130%;
    padding:10px;
    border-radius: 7px;
    opacity:0.4;
    border-bottom: 1px solid white;
    margin-bottom: 20px;
}

.card_display
{
    display:flex;
    flex-direction:row;
}

.card_element
{
    border:1px solid white;
    margin:1%;
    padding:1%;
    cursor:pointer;
    border-radius:5px;
    box-shadow:2px 3px 3px gainsboro;
    background-color:white;
    color:black;
    height:200px;
    width:20%;
    transition:0.2s ease-in-out;
    text-decoration: none;
}

h3
{
    font-weight:200;
    font-size:16px;
}

.card_element:hover
{
    background-color:rgba(211, 198, 198, 0.473);
    box-shadow:2px 3px 3px rgba(190, 179, 179, 0.473);
    color:white;
    border:none;
}